.videoEdit{
    display: grid;
    grid-template-columns: 1fr 2fr;;
    box-shadow: 2px 2px 40px 0px rgba(0, 0, 0, 0.2);
    border-radius: 30px;
    width: 700px;
    height: 30vh;
    align-items: center;
    
}

.videoLeft{
    display: flex;
    flex-direction: column;
    padding: 1.5rem;
    padding-left: 2rem;
    
}
.videoRight{
    display: flex;
    flex-direction: column;
    padding-left: 100px;

}


.ps{
    display: flex;

    align-items: center;
    padding-bottom: 1.3rem;
}

.ps-outer{
    width: 200px;
    height: 20px;
    border: 1px solid #929292;
    border-radius: 10px;
    position: absolute;
}
.ps-inner{
    
    height: 20px;
    background-color: #929292;
    position: relative;
    border-radius: 10px;
    animation: react-animate 1s forwards;
}

@keyframes ps-animate{
    0%{width: 0px;}
    100%{width: 150px;}
}

